.Pic:hover{
    /* border: solid 3px rgb(197, 195, 195); */
    -webkit-box-shadow: 0 0 40px rgb(177, 177, 240);
            box-shadow: 0 0 40px rgb(177, 177, 240);
    -webkit-transform: scale(0.9);
        -ms-transform: scale(0.9);
